import { Module, ModuleCriteria, ModuleFilter } from '../Types.ts';

export function createFilter(criteria: ModuleCriteria): ModuleFilter {
    console.log(criteria.grades.includes('A+'));
    return (module: Module) => 
        (criteria.years.length === 0        || criteria.years.includes(module.year))
        && (criteria.semesters.length === 0 || criteria.semesters.includes(module.semester))
        && (criteria.grades.length === 0    || criteria.grades.includes(module.grade))
        && (criteria.moduleCode === ''      || module.moduleCode.includes(criteria.moduleCode.toUpperCase()))
        && (criteria.unit === undefined     || criteria.unit === module.unit)
        && (criteria.remark === ''          || module.remark.toLowerCase().includes(criteria.remark.toLowerCase()));
};

export function orFilter(filter1: ModuleFilter, filter2: ModuleFilter): ModuleFilter {
    return (module: Module) => filter1(module) || filter2(module);
}

export function andFilter(filter1: ModuleFilter, filter2: ModuleFilter): ModuleFilter {
    return (module: Module) => filter1(module) && filter2(module);
}
